const mode = 'dev'

if(mode === 'dev') {
    const debug_element = `
    <style type="text/css" id="debug-rules"></style>
        <div class="debug">
            Debug border:
            <button class="debug-btn">On</button>
            <button class="close-btn">Hide</button>
        </div>
    `
    document.body.insertAdjacentHTML( 'afterbegin', debug_element );

    const style_container = document.getElementById('debug-rules').sheet

    const css_rule = `
    * {
        outline: 1px solid red;
    }`

    const panel = document.getElementsByClassName('debug')[0]
    const button = document.getElementsByClassName('debug-btn')[0]
    const close_button = document.getElementsByClassName('close-btn')[0]

    button.addEventListener('click', function(e) {
        button.textContent === 'On' ?
        (button.textContent = 'Off', style_container.insertRule(css_rule, 0)) :
        (button.textContent = 'On', style_container.deleteRule(0));
    }, false)

    close_button.addEventListener('click', function(e) {
        panel.style.display = 'none'
    }, false)
}


function isElement(element) {
    return !!document.querySelector(element) ? true : null
}

if(isElement('.case-swiper')){
    const swiper = new Swiper(".swiper", {
        pagination: {
            el: ".swiper-pagination",
            type: "fraction",
            dynamicBullets: true
        },
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });
}

const unique_swiper_config = {
    pagination: {
        el: ".swiper-pagination",
        type: "fraction",
        dynamicBullets: true
    },
    navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
    },
    slidesPerView: 3,
    spaceBetween: 35,
    breakpoints: {
        768: {
            slidesPerView: 1
        },
        992: {
            slidesPerView: 2
        },
        1200: {
            slidesPerView: 3
        }
    }
}

if(isElement('.unique-swiper')){
    const swiper = new Swiper(".swiper", unique_swiper_config);
}

if(isElement('#map')){
    function initMap() {
        const point = { lat: -25.344, lng: 131.036 };
        const map = new google.maps.Map(document.getElementById("map"), {
            zoom: 4,
            center: point,
        });
        const marker = new google.maps.Marker({
            position: point,
            map: map,
        });
    }
}


if(isElement('.work-item')) {
    const work_items = document.getElementsByClassName('work-item')
    const absolute_items = document.getElementsByClassName('absolute-info')
    const parent = document.getElementsByClassName('how-we-work-section')[0]

    let highest_height = null
    for (let i = 0; i < work_items.length; i++) {

        if(!highest_height) {
            highest_height = absolute_items[i].scrollHeight + 40
        } else if(absolute_items[i].scrollHeight + 40 > highest_height) {
            highest_height = absolute_items[i].scrollHeight + 40
        }

        absolute_items[i].style.maxHeight = 0
        absolute_items[i].style.paddingTop = 0
        absolute_items[i].style.paddingBottom = 0

        work_items[i].addEventListener('mouseover', function(e) {
            absolute_items[i].style.paddingTop = '20px'
            absolute_items[i].style.paddingBottom = '20px'
            absolute_items[i].style.maxHeight =  absolute_items[i].scrollHeight + 40 + 'px'

        })
        work_items[i].addEventListener('mouseleave', function(e) {
            absolute_items[i].style.maxHeight = 0
            absolute_items[i].style.paddingTop = 0
            absolute_items[i].style.paddingBottom = 0

        })
    }
    parent.style.paddingBottom = highest_height * 2 + 40 + 'px'
}

function setFontSize(value = 30){
    const width = window.innerWidth
    const height = window.innerHeight
    const target = document.getElementsByClassName('work-item')

    for (let i = 0; i < target.length; i++) {
        target[i].style.fontSize = width/height * value + 'px'
    }

}

function adaptiveSteps() {
    const font_value = 29
    setFontSize(font_value)
    window.addEventListener('resize', function(){
        setFontSize(font_value)
    })
}
adaptiveSteps()



function languageDropdown() {
    const languages = document.querySelectorAll('.languages li')
    languages.forEach((lang) => {
        lang.addEventListener('click', function(e){
            languages.forEach((lang) => {
                lang.classList.toggle('visible')
            })
        })
    })
}
languageDropdown()
